.complete {
    .result {
        &__img {
            position: absolute;
            &:nth-child(1) {
                top: 0;
                left: 0px;
                width: 8%;
            }

            &:nth-child(2) {
                top: 0;
                right: 50px;
                width: 12%;
            }
            &:nth-child(3) {
                top: 15%;
                left: 20%;
                width: 5%;
            }
            &:nth-child(4) {
                bottom: 15%;
                left: 5%;
                width: 10%;
            }
            &:nth-child(5) {
                bottom: 0;
                right: 20%;
                width: 8%;
            }
        }
    }
}


@media (max-width:990px) {
    .complete {
        .result {
            &__img {
                &:nth-child(3) {
                    display: none;
                }
            }
        }
    }
    
}

@media (max-width:690px) {
    .complete {
        .result {
            &__img {
                &:nth-child(1) {
                    display: none;
                }
                &:nth-child(2) {
                    display: none;
                }
                &:nth-child(4) {
                   display: none;
                }
                &:nth-child(5) {
                    display: none;
                }
            }
        }
    }
}

